	module.exports = {"data":{"markdownRemark":{"html":"<p>At that time I had ambition saying \"I want to make RPG on PC!\" ... but it has just been settled in about one or two weeks.</p>\n<p>As a reason for that, my technology is not available, but I think that the limits of the development language \"N88 - BASIC (86)\" that I was using at the time have come to light. (There is also the theory saying that there was no ozum of degree to consider the story at that time ^ ^;)</p>\n<p>It is an RPG that made the development language and the limits of own omu thoroughly.</p>\n<p>The aim of this RPG ...... That is what the title says w, that is, the hero's treasure hunter has heard the story saying a treasure is falling asleep in this castle.</p>\n<p>At the next moment the hero of an inorganic physique stands at the entrance of the castle. <em>And there is tremendous treasure in the example in front!</em> Of course, you can take it home as it is ... ... but the thing that is still RPG is surely what you have rasubosu, the moment I got treasure without really pursuing trainee, I <em>drew it</em> with <em>CIRCLE and LINE sentences</em> like screen picture (reproduction) <em>It is immediate end as it is eaten exorbitant damage to such objects.</em></p>\n<p>For that reason it is essentially an RPG for <em>the purpose that said \"I will go around in the castle in order to drive away the guardian god of a stabbed treasure to train in the castle\"</em> rather than saying \"get treasure\". And since there are I, I think that there is II, but actually I have not yet put out with a project alone.</p>\n<h2>Although it is just a replacement of the map and the enemy though it is w</h2>\n<ul>\n<li>Genre: RPG</li>\n<li>Development language: N88 - Japanese BASIC (86) MS - DOS version</li>\n<li>Last Updated: 1996 Summer</li>\n<li>Because of source loss, there is no distribution.</li>\n</ul>\n<p>As an aside, the <a href=\"../../1995/11/01-treasure-in-grave.md\">opening movie was also delusional,</a> I was thinking about <a href=\"../../1995/11/01-treasure-in-grave.md\">image illustration of</a> it. Also, it is said that how this was reflected in the game.</p>","frontmatter":{"cover":null,"date":"1996-07-01T00:00:07+09:00","strDate":"1996/6/30","title":"Treasure of the destroyed castle Ⅰ"}}},"pathContext":{"path":"/en/blog/1996/07/01-treasure-in-grave/","langKey":"en"}}

